/*==================================================================
  Form Wrappers
===================================================================*/
.admin-form,
.admin-form * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.admin-form {
  line-height: 1.2312;
  font-weight: 400;
  font-size: 14px;
  color: #797b7c;
}
.admin-form .section {
  margin-bottom: 22px;
}
/*==================================================================
  Browser Styling Reset
===================================================================*/
.admin-form .gui-input,
.admin-form .gui-textarea,
.admin-form .select > select,
.admin-form input[type="button"],
.admin-form input[type="submit"],
.admin-form input[type="search"],
.admin-form .select-multiple select {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.admin-form input[type="search"]::-webkit-search-decoration,
.admin-form input[type="search"]::-webkit-search-cancel-button,
.admin-form input[type="search"]::-webkit-search-results-button,
.admin-form input[type="search"]::-webkit-search-results-decoration {
  display: none;
}
/*==================================================================
  Prevent user selection. For usability purposes
===================================================================*/
.admin-form .radio,
.admin-form .button,
.admin-form .checkbox,
.admin-form .select .arrow,
.admin-form .switch > label,
.admin-form .ui-slider .ui-slider-handle {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/*==================================================================
  Input General Styles
===================================================================*/
.admin-form label,
.admin-form input,
.admin-form button,
.admin-form select,
.admin-form textarea {
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  color: #797b7c;
  outline: none;
}
/*==================================================================
  Input Animations
===================================================================*/
.admin-form .radio,
.admin-form .button,
.admin-form .tooltip,
.admin-form .checkbox,
.admin-form .gui-input,
.admin-form .input-footer,
.admin-form .notification,
.admin-form .gui-textarea,
.admin-form .select > select,
.admin-form .select-multiple select {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  outline: none;
}
.admin-form .input-link,
.admin-form .panel-heading,
.admin-form .section-divider span,
.admin-form .select > select,
.admin-form .select-multiple select {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
/*===============================================
  Form Dividers
================================================= */
.admin-form .spacer {
  border-top: 1px solid #eee;
  margin-top: 10px;
  margin-bottom: 10px;
  display: block;
  height: 0;
}
/*==================================================================
  Form Text Elements
===================================================================*/
/* Themed Link */
.admin-form .theme-link {
  color: #661A00;
  text-decoration: none;
}
.admin-form .theme-link:hover {
  text-decoration: underline;
}
/* Section Divider */
.admin-form .section-divider {
  height: 0;
  border-top: 1px solid #949596;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 30px;
}
.admin-form .section-divider span {
  display: inline-block;
  position: relative;
  padding: 0 17px;
  background: #e9e9e9;
  color: #999;
  top: -11px;
  font-size: 15px;
}
.admin-form.panel .section-divider span,
.admin-form .panel .section-divider span,
.admin-form.tab-content .section-divider span,
.admin-form .tab-content .section-divider span {
  background: #fff;
}
.admin-form.bg-light .section-divider span,
.admin-form .bg-light .section-divider span {
  background: #FAFAFA;
}
.admin-form.bg-light.light .section-divider span,
.admin-form .bg-light.light .section-divider span {
  background: #FEFEFE;
}
.admin-form.bg-light.dark .section-divider span,
.admin-form .bg-light.dark .section-divider span {
  background: #F2F2F2;
}
.admin-form.well .section-divider span,
.admin-form .well .section-divider span {
  background: #f5f5f5;
}
/* Input Labels */
.admin-form .switch,
.admin-form .option,
.admin-form .field-label {
  font-size: 14px;
}
/* Column Labels - Can also use .field-align */
.admin-form .field-label[class*='col-'] {
  line-height: 42px;
}
.admin-form .field-align {
  line-height: 42px;
}
/*==================================================================
  General Input Styling 
===================================================================*/
.admin-form .radio,
.admin-form .option,
.admin-form .checkbox {
  cursor: pointer;
}
.admin-form .field {
  display: block;
  position: relative;
}
.admin-form .field-icon i {
  color: #BBB;
  position: relative;
}
.admin-form .field-label {
  display: block;
  margin-bottom: 7px;
}
.admin-form .field-label em {
  color: #e74c3c;
  font-size: 14px;
  font-style: normal;
  display: inline-block;
  margin-left: 4px;
  position: relative;
  top: 3px;
}
.admin-form .gui-input,
.admin-form .gui-textarea {
  padding: 10px;
}
.admin-form .select,
.admin-form .gui-input,
.admin-form .gui-textarea,
.admin-form .select > select,
.admin-form .select-multiple select {
  position: relative;
  vertical-align: top;
  border: 1px solid #bcbdbd;
  display: -moz-inline-stack;
  display: inline-block;
  *display: inline;
  outline: none;
  color: #797b7c;
  height: 39px;
  width: 100%;
}
.admin-form .select::-moz-placeholder,
.admin-form .gui-input::-moz-placeholder,
.admin-form .gui-textarea::-moz-placeholder,
.admin-form .select > select::-moz-placeholder,
.admin-form .select-multiple select::-moz-placeholder {
  color: #797b7c;
  opacity: 1;
}
.admin-form .select:-ms-input-placeholder,
.admin-form .gui-input:-ms-input-placeholder,
.admin-form .gui-textarea:-ms-input-placeholder,
.admin-form .select > select:-ms-input-placeholder,
.admin-form .select-multiple select:-ms-input-placeholder {
  color: #797b7c;
}
.admin-form .select::-webkit-input-placeholder,
.admin-form .gui-input::-webkit-input-placeholder,
.admin-form .gui-textarea::-webkit-input-placeholder,
.admin-form .select > select::-webkit-input-placeholder,
.admin-form .select-multiple select::-webkit-input-placeholder {
  color: #797b7c;
}
.admin-form .select option {
  background: #fff;
}
.admin-form select[disabled],
.admin-form .select > select[disabled] {
  color: #aaa !important;
}
/*==================================================================
 Form Textarea
===================================================================*/
.admin-form .gui-textarea {
  resize: none;
  line-height: 19px;
  overflow: auto;
  max-width: 100%;
  height: 96px;
}
/*==================================================================
  Form Selects
===================================================================*/
.admin-form .select {
  border: 0;
  z-index: 10;
  background: #FFF;
}
.admin-form .select > select {
  display: block;
  z-index: 10;
  margin: 0;
  padding: 9px 10px;
  color: #797b7c !important;
  outline: none;
  text-indent: 0.01px;
  text-overflow: '';
  background: #FFF;
  border: 1px solid #DDD;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: normal;
}
.admin-form .select:before,
.admin-form .select:after {
  background: #FFF;
}
.admin-form .select > select::-ms-expand,
.bs-component select::-ms-expand {
  display: none;
}
.admin-form .select > select,
.bs-component select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
.admin-form .select .arrow,
.bs-component .arrow {
  pointer-events: none;
  position: absolute;
  top: 9px;
  right: 4px;
  width: 24px;
  height: 24px;
  color: #9F9F9F;
  z-index: 100;
}
.admin-form .select .arrow:after,
.admin-form .select .arrow:before,
select.form-control + .arrow:before,
select.form-control + .arrow:after {
  content: '';
  position: absolute;
  font: 16px "Consolas", monospace;
  font-style: normal;
  pointer-events: none;
  left: 5px;
}
.admin-form .select .arrow:before {
  content: '\25BC';
  bottom: 4px;
}
.admin-form .select .double:after {
  content: '\25B2';
  top: -6px;
}
.admin-form .select .double:before {
  content: '\25BC';
  bottom: -3px;
}
select.form-control + .arrow:after {
  content: '\25B2';
  top: -6px;
}
select.form-control + .arrow:before {
  content: '\25BC';
  bottom: -3px;
}
.admin-form .select-multiple select {
  width: 100%;
  height: 123px;
  padding: 10px;
}
/*==================================================================
  Form File Inputs
===================================================================*/
.admin-form .file {
  display: block;
  width: 100%;
}
.admin-form .file .gui-file {
  width: 100%;
  height: 100%;
  cursor: pointer;
  padding: 8px 10px;
  position: absolute;
  -moz-opacity: 0;
  opacity: 0;
  z-index: 11;
  bottom: 0;
  right: 0;
}
/* Default state positions button right */
.admin-form .file .button {
  position: absolute;
  top: 1px;
  right: 1px;
  float: none;
  height: 37px;
  line-height: 34px;
  padding: 0 16px;
  z-index: 10;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
/* File Button - Left Style */
.admin-form .file.append-button .button {
  left: 1px;
  right: auto;
}
.admin-form .file.append-button > input.gui-input {
  padding-right: 40px;
  padding-left: 125px;
  text-align: right;
}
.admin-form .file.append-button > label.field-icon {
  right: 4px;
  left: auto;
}
/*==================================================================
  Form Inputs:hover state
-===================================================================*/
.admin-form .gui-input:hover,
.admin-form .gui-textarea:hover,
.admin-form .select > select:hover,
.admin-form .select-multiple select:hover,
.admin-form .gui-input:hover ~ .input-footer,
.admin-form .file .gui-file:hover + .gui-input,
.admin-form .gui-textarea:hover ~ .input-footer {
  border-color: #888;
}
/*==================================================================
  Form Inputs:focus state
===================================================================*/
.admin-form .gui-input:focus,
.admin-form .gui-textarea:focus,
.admin-form .select > select:focus,
.admin-form .select-multiple select:focus {
  color: #3c3c3c;
  background: #fff;
  border: 1px solid #999;
  outline: none;
}
.admin-form .select > select:focus {
  z-index: 10;
  z-index: 20\9;
}
.admin-form .gui-textarea:focus {
  height: 120px;
}
.admin-form .select > select:focus {
  z-index: 10;
  z-index: 20\9;
}
.admin-form .gui-input:focus ~ .field-icon i,
.admin-form .gui-textarea:focus ~ .field-icon i {
  color: #999;
}
.admin-form .select-multiple select:focus,
.admin-form .gui-input:focus ~ .input-footer,
.admin-form .gui-textarea:focus ~ .input-footer,
.admin-form .file .gui-file:focus + .gui-input {
  border-color: #999;
}
.admin-form .select > select:focus + .arrow {
  color: #999;
}
/*==================================================================
  Radio/Checkboxes
-===================================================================*/
.admin-form .option {
  position: relative;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
}
.admin-form .option > input {
  position: absolute;
  height: inherit;
  width: inherit;
  opacity: 0;
  left: 0;
}
.admin-form .checkbox,
.admin-form .radio {
  cursor: pointer;
  position: relative;
  margin-right: 5px;
  background: #fff;
  display: inline-block;
  border: 3px solid #a1a3a4;
  height: 21px;
  width: 21px;
  top: 4px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
.admin-form .checkbox:before,
.admin-form .radio:before {
  content: '';
  display: none;
}
.admin-form input:checked + .checkbox:before,
.admin-form input:checked + .radio:before {
  display: block;
}
.admin-form .checkbox:before {
  position: absolute;
  top: 4px;
  left: 3px;
  width: 6px;
  height: 3px;
  border: solid #888;
  border-width: 0 0 3px 3px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.admin-form input:checked + .checkbox,
.admin-form input:checked + .radio {
  border: 3px solid #888;
}
.admin-form .radio {
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -o-border-radius: 20px;
  border-radius: 20px;
}
.admin-form .radio:before {
  margin: 4px;
  width: 7px;
  height: 7px;
  background: #888;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -o-border-radius: 10px;
  border-radius: 10px;
}
/*==================================================================
  Radio/Checkboxes:hover state 
===================================================================*/
.admin-form input:not([disabled]):hover + .checkbox,
.admin-form input:not([disabled]):hover + .radio {
  border-color: #666;
}
/*==================================================================
  Radio/Checkboxes:focus state 
-===================================================================*/
.admin-form input:focus + .checkbox,
.admin-form input:focus + .radio {
  border-color: #666;
}
.admin-form input:focus + .radio:before {
  background: #666;
}
.admin-form input:focus + .checkbox:before {
  border-color: #666;
}
/*==================================================================
  Switches
===================================================================*/
.admin-form .switch {
  cursor: pointer;
  position: relative;
  padding-right: 10px;
  display: inline-block;
  margin-bottom: 5px;
  height: 26px;
}
.admin-form .switch > label {
  cursor: pointer;
  display: inline-block;
  position: relative;
  height: 25px;
  width: 58px;
  color: #fff;
  font-size: 10px;
  font-weight: bold;
  line-height: 20px;
  text-align: center;
  background: #D7D7D7;
  border: 2px solid #D7D7D7;
  text-transform: uppercase;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-transition: 0.3s ease-out;
  -moz-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.admin-form .switch > label + span {
  display: inline-block;
  padding-left: 5px;
  position: relative;
  top: -7px;
}
.admin-form .switch > label:before {
  content: attr(data-off);
  position: absolute;
  top: 1px;
  right: 3px;
  width: 33px;
}
.admin-form .switch > label:after {
  content: "";
  margin: 1px;
  width: 19px;
  height: 19px;
  display: block;
  background: #fff;
  -webkit-border-radius: 2px;
  border-radius: 2px;
}
.admin-form .switch > input {
  -webkit-appearance: none;
  position: absolute;
  width: inherit;
  height: inherit;
  opacity: 0;
  left: 0;
  top: 0;
}
/*==================================================================
  Switch:focus state
-===================================================================*/
.admin-form .switch > input:focus {
  outline: none;
}
.admin-form .switch > input:focus + label {
  color: #fff;
  border-color: #C7C7C7;
  background: #C7C7C7;
}
.admin-form .switch > input:focus + label:after {
  background: #fff;
}
/*==================================================================
  Switch:checked state
===================================================================*/
.admin-form .switch > input:checked + label {
  border-color: #999;
  background: #999;
  padding-left: 33px;
  color: white;
}
.admin-form .switch > input:checked + label:before {
  content: attr(data-on);
  left: 1px;
  top: 1px;
}
.admin-form .switch > input:checked + label:after {
  margin: 1px;
  width: 19px;
  height: 19px;
  background: white;
}
/*==================================================================
  Switch:checked:focus state
-===================================================================*/
.admin-form .switch > input:checked:focus + label {
  background: #555;
  border-color: #555;
}
/* Round Style*/
.admin-form .switch-round > label {
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  -o-border-radius: 13px;
  border-radius: 13px;
}
.admin-form .switch-round > label + span {
  top: -2px;
}
.admin-form .switch-round > label:before {
  width: 33px;
}
.admin-form .switch-round > label:after {
  width: 19px;
  color: #D7D7D7;
  content: "\2022";
  font: 20px/20px Times, Serif;
  -webkit-border-radius: 13px;
  -moz-border-radius: 13px;
  -o-border-radius: 13px;
  border-radius: 13px;
}
.admin-form .switch-round > input:checked + label {
  padding-left: 33px;
}
.admin-form .switch-round > input:checked + label:after {
  color: #999;
}
/*==================================================================
  Switch - Custom Success/Danger Styled
-===================================================================*/
.admin-form .switch-custom > label {
  background: #fdac84;
  border-color: #fdac84;
}
.admin-form .switch-custom > input:hover + label,
.admin-form .switch-custom > input:focus + label {
  background: #fb5e11;
  border-color: #fb5e11;
}
/* Checked state */
.admin-form .switch-custom > input:checked + label {
  background: #6fbb47;
  border-color: #6fbb47;
}
.admin-form .switch-custom > input:checked:hover + label,
.admin-form .switch-custom > input:checked:focus + label {
  background: #4e8531;
  border-color: #4e8531;
}
/*==================================================================
  Input:disabled state
===================================================================*/
.admin-form .button[disabled],
.admin-form .state-disabled .button,
.admin-form input[disabled] + .radio,
.admin-form input[disabled] + .checkbox,
.admin-form .switch > input[disabled] + label {
  cursor: default;
  opacity: 0.5;
}
.admin-form .gui-input[disabled],
.admin-form .gui-textarea[disabled],
.admin-form .select > select[disabled],
.admin-form .select-multiple select[disabled],
.admin-form .gui-input[disabled] ~ .input-footer,
.admin-form .file .gui-file[disabled] + .gui-input,
.admin-form .file .gui-file[disabled]:hover + .gui-input,
.admin-form .gui-textarea[disabled] ~ .input-footer {
  background-color: #f1f2f2;
  border-color: #d3d4d4 !important;
  cursor: default;
  color: rbga(121, 123, 124, 0.5);
  opacity: 1;
}
.admin-form input[disabled] ~ .field-icon i,
.admin-form textarea[disabled] ~ .field-icon i,
.admin-form .select > select[disabled] + .arrow {
  opacity: 1;
}
/*==================================================================
  Form Footer
===================================================================*/
.admin-form .input-footer {
  padding: 10px;
  display: block;
  margin-top: -1px;
  line-height: 16px;
  position: relative;
  background: #F5F5F5;
  border: 1px solid #DDD;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -o-border-radius: 0;
  border-radius: 0;
  font-size: 11px;
  color: #999;
}
/*==================================================================
  Input Icons
===================================================================*/
.admin-form .append-icon,
.admin-form .prepend-icon {
  top: 0;
  left: 0;
  display: inline-block;
  vertical-align: top;
  position: relative;
  width: 100%;
}
.admin-form .append-icon .field-icon,
.admin-form .prepend-icon .field-icon {
  top: 0;
  z-index: 4;
  width: 42px;
  height: 38px;
  color: inherit;
  position: absolute;
  text-align: center;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -ms-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  pointer-events: none;
}
.admin-form .append-icon .field-icon i,
.admin-form .prepend-icon .field-icon i {
  position: relative;
  font-size: 14px;
  top: 10px;
}
.admin-form .prepend-icon .field-icon {
  left: 0;
}
.admin-form .append-icon .field-icon {
  right: 0;
}
.admin-form .prepend-icon > input,
.admin-form .prepend-icon > textarea {
  padding-left: 36px;
}
.admin-form .append-icon > input,
.admin-form .append-icon > textarea {
  padding-right: 36px;
  padding-left: 10px;
}
.admin-form .append-icon > textarea {
  padding-right: 36px;
}
/*==================================================================
  Input Tooltips
===================================================================*/
.admin-form .tooltip {
  position: absolute;
  z-index: -1;
  opacity: 0;
  color: #fff;
  width: 184px;
  left: -9999px;
  top: auto;
  font-size: 11px;
  font-weight: normal;
  background: #666;
  -webkit-transition: margin 0.6s, opacity 0.6s;
  -moz-transition: margin 0.6s, opacity 0.6s;
  -ms-transition: margin 0.6s, opacity 0.6s;
  -o-transition: margin 0.6s, opacity 0.6s;
  transition: margin 0.6s, opacity 0.6s;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.admin-form .tooltip > em {
  padding: 12px;
  font-style: normal;
  display: block;
  position: static;
}
.admin-form .tooltip:after {
  content: '';
  position: absolute;
}
.admin-form .gui-input:focus + .tooltip,
.admin-form .gui-textarea:focus + .tooltip {
  opacity: 1;
  z-index: 999;
}
/*==================================================================
  Tooltip - Left
===================================================================*/
.admin-form .tip-left {
  top: 1px;
  margin-right: -20px;
}
.admin-form .tip-left:after {
  top: 12px;
  left: 100%;
  border-left: 8px solid #666;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
}
.admin-form .gui-input:focus + .tip-left,
.admin-form .gui-textarea:focus + .tip-left {
  margin-right: 5px;
  right: 100%;
  left: auto;
}
/*==================================================================
  Tooltip - Right
===================================================================*/
.admin-form .tip-right {
  top: 1px;
  margin-left: -20px;
}
.admin-form .tip-right:after {
  top: 12px;
  right: 100%;
