/*==================================================================
  AdminForm Wizard
===================================================================*/
/* form wizard wrapper */
.wizard {
  background-color: #FFF;
}
/* Wizard top nav - "steps" */
.wizard .steps {
  text-align: center;
  padding: 20px 15px 10px;
  border: 1px solid #E8E8E8;
  border-bottom: 0;
}
.wizard .steps ul,
.wizard .steps li {
  list-style: none;
  text-decoration: none;
  padding: 0;
  margin: 0;
}
.wizard .steps li {
  display: inline-block;
  margin-right: 10px;
}
.wizard .steps li.last {
  margin-right: 0;
}
.wizard .steps li.disabled a {
  cursor: not-allowed;
}
.wizard .steps li a {
  display: inline-block;
  color: #FFF;
  font-size: 15px;
  text-align: center;
  padding: 12px 40px;
  background-color: #852200;
  text-decoration: none;
  -webkit-user-drag: none;
}
/* Nav item states */
.wizard .steps li.current a {
  background-color: #661A00;
}
.wizard .steps li.disabled a {
  background-color: #BBBBBB !important;
}
.wizard .steps li.current a:focus {
  outline-color: transparent;
}
/* Disable various nav meta information */
.wizard .steps li a .current-info,
.wizard .steps li a .number {
  display: none;
}
/* Wizard Content */
.wizard .content {
  background-color: #ffffff;
  border: 1px solid #E8E8E8;
  border-top: 0;
}
.wizard .content + .actions {
  border-top: 0;
}
.wizard .wizard-section-title {
  display: none;
}
.wizard .wizard-section.body {
  padding: 15px;
}
/* Wizard Actions/Footer */
.wizard .actions {
  padding: 10px 15px;
  background-color: #f8f8f8;
  border: 1px solid #E8E8E8;
}
.wizard .actions ul {
  list-style: none;
  text-decoration: none;
  padding: 0;
  margin: 0;
  float: right;
}
.wizard .actions li {
  display: inline-block;
  margin-left: 10px;
}
.wizard .actions li a {
  display: inline-block;
  color: #FFF;
  padding: 9px 18px;
  background-color: #661A00;
  text-decoration: none;
  -webkit-user-drag: none;
}
.wizard .actions li.disabled a {
  cursor: not-allowed;
  background-color: #BBB !important;
}
/* hide steps icons */
.wizard.steps-hide-icons .steps .fa,
.wizard.steps-hide-icons .steps .glyphicon,
.wizard.steps-hide-icons .steps .glyphicon {
  display: none !important;
}
/* hide steps icons */
.wizard.steps-show-icons .steps .fa,
.wizard.steps-show-icons .steps .glyphicon,
.wizard.steps-show-icons .steps .glyphicon {
  display: inline-block !important;
}
/* steps align right  */
.wizard.steps-left .steps {
  text-align: left;
}
/* steps align right  */
.wizard.steps-right .steps {
  text-align: right;
}
/* steps justified */
.wizard.steps-justified .steps {
  position: relative;
  width: 100%;
}
.wizard.steps-justified .steps li {
  display: table-cell;
  position: relative;
  float: none;
  width: 1%;
}
.wizard.steps-justified .steps li a {
  display: block;
}
/* steps tabs */
.wizard.steps-tabs .steps {
  padding: 0;
  height: 55px;
  position: relative;
  background-color: #EEE;
  border-color: transparent;
  border-bottom: 1px solid #E8E8E8;
}
.wizard.steps-tabs .steps li {
  margin: 0;
}
.wizard.steps-tabs .steps li a {
  padding: 18px 23px 18px;
}
/* steps add BG */
.wizard.steps-bg .steps {
  padding: 15px;
  position: relative;
  background-color: #f8f8f8;
  border-bottom: 1px solid #E8E8E8;
}
.wizard.steps-bg.steps-tabs .steps {
  padding: 0;
  border-color: #E8E8E8;
}
.wizard.steps-bg .content,
.wizard.steps-tabs .content,
.wizard.steps-justified .content {
  padding-top: 10px;
}
